body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.center {
  margin: 0;
  position:relative;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.f-height{
  margin-bottom: 150px;
}
.c-mb{
  margin-top: 50px;
}

.l-card {
   margin: 10px;
}

.l-card-image{
  border-radius: 10px;
  width: 300px;
  height: 450px;
  box-shadow: 0px 0px 3px 1px #CCC;
}

 .l-card-info{
   margin-top: 10px;
   min-height: 100px;
   max-width: 325px;

 }

 .l-card-title{
  font-size: 24px;
  margin: 0px;
 }
 
 .l-card-subtitle{
   font-size: 16px;
   margin: 0px;
 }

 .success-msg{
   color: #57A773;
   padding: 10px 15px;
 }

 .err-msg{
  color: #EE6352;
  padding: 10px 15px;
}